<template>
    <ul class="asideCom_box">
        <li v-for="ele in getItem" :key="ele.index">
            {{ ele.title }}
        </li>
    </ul>
</template>

<script>
export default {
    name: "asideCom",
    props: {
        isM: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            items: [
                {
                  icon: "el-icon-s-home",
                  index: "aaa",
                  title: ""
                },
                {
                  icon: "el-icon-menu",
                  index: "table",
                  title: ""
                },
                {
                  icon: "el-icon-document-copy",
                  index: "tabs",
                  title: "tab"
                },
                {
                  icon: "el-icon-set-up",
                  index: "3",
                  title: "",
                  subs: [
                    {
                      index: "form",
                      title: ""
                    },
                    {
                      index: "3-2",
                      title: "",
                      subs: [
                        {
                          index: "editor",
                          title: ""
                        },
                        {
                          index: "markdown",
                          title: "markdown"
                        }
                      ]
                    },
                    {
                      index: "upload",
                      title: ""
                    }
                  ]
                },
                {
                  icon: "el-icon-document-checked",
                  index: "icon",
                  title: ""
                },
                {
                  icon: "el-icon-pie-chart",
                  index: "charts",
                  title: "schart"
                },
                {
                  icon: "el-icon-rank",
                  index: "6",
                  title: "",
                  subs: [
                    {
                      index: "drag",
                      title: ""
                    },
                    {
                      index: "dialog",
                      title: ""
                    }
                  ]
                },
                {
                  icon: "el-icon-s-help",
                  index: "i18n",
                  title: ""
                },
                {
                  icon: "el-icon-warning",
                  index: "7",
                  title: "",
                  subs: [
                    {
                      index: "permission",
                      title: ""
                    },
                    {
                      index: "404",
                      title: "404"
                    }
                  ]
                },
                {
                  icon: "el-icon-s-goods",
                  index: "/donate",
                  title: ""
                }
            ]
        }
    },
    computed: {
        getItem() {
            if (this.isM && this.items.length > 5) {
                // eslint-disable-next-line vue/no-side-effects-in-computed-properties
                return (this.items.length = 5) && this.items;
            } else {
                return this.items;
            }
        }
    },
}
</script>